* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.App{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
body {
  background: #eee; }
  body div.App div.form {
    padding: 40px;
    border: solid 0.5px #adb9c1;
    background: white; }
    body div.App div.form div.formRow {
      margin: 10px 0; }
    body div.App div.form h2,
    body div.App div.form p {
      text-align: center; }
    body div.App div.form input,
    body div.App div.form button {
      padding: 13px 20px;
      font-size: 14px; }
    body div.App div.form input {
      width: 228px; }
    body div.App div.form button {
      width: 100%; }
  body div.App .loading {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    display: flex;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    justify-content: center; }

.tooltip {
  position: fixed;
  text-align: center;
  top: 10px;
  display: flex;
  justify-content: center;
  width: 100%; }
  .tooltip span {
    padding: 5px; }
    .tooltip span.success {
      background: lightgreen; }
    .tooltip span.error {
      background: lightcoral; }

.sidebar {
  text-align: center;
  width: 70px;
  background: #fcfdfb;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .sidebar .username {
    word-wrap: break-word;
    color: #dbdbdb;
    margin-bottom: 15px; }
  .sidebar svg {
    width: 40%;
    height: 50px;
    fill: #dbdbdb;
    cursor: pointer; }

.dialog .mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.3); }

.dialog .dialogContent {
  background: white;
  border: 1px solid black;
  border-radius: 4px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 300px; }
  .dialog .dialogContent > * {
    margin: 10px; }
  .dialog .dialogContent .cancel {
    position: absolute;
    right: 0;
    cursor: pointer; }
  .dialog .dialogContent input {
    border: 1px solid black;
    width: 100%; }
  .dialog .dialogContent .agree {
    margin-right: 10px; }

.roster {
  width: 200px;
  border-right: 1px solid #c2c2c2;
  border-left: 1px solid #c2c2c2;
  overflow: auto;
  text-decoration: none; }
  .roster .friendItem {
    cursor: pointer;
    padding: 10px;
    color: gray;
    border-bottom: 1px solid #c2c2c2;
    display: flex;
    flex-direction: row;
    align-items: center; }
    .roster .friendItem.selected {
      background: aliceblue; }
    .roster .friendItem svg {
      width: 40px;
      height: 40px;
      fill: #dbdbdb;
      margin-right: 10px; }
    .roster .friendItem .username,
    .roster .friendItem .preview {
      width: 130px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap; }

.panel {
  width: 500px;
  display: flex;
  flex-direction: column; }
  .panel .currentSession {
    text-align: center;
    padding: 10px;
    border-bottom: 1px solid #c2c2c2; }
  .panel .messageList {
    flex: 3 1;
    overflow: auto;
    word-break: break-all; }
    .panel .messageList .messageItem {
      display: flex;
      margin: 15px; }
      .panel .messageList .messageItem > div > div {
        margin: 5px; }
      .panel .messageList .messageItem .messageText {
        border-radius: 10px;
        background: #c2c2c2;
        padding: 8px; }
      .panel .messageList .messageItem svg {
        width: 40px;
        height: 40px; }
      .panel .messageList .messageItem.reverse {
        flex-direction: row-reverse; }
        .panel .messageList .messageItem.reverse .messageAuthor {
          display: flex;
          justify-content: flex-end; }
        .panel .messageList .messageItem.reverse .messageText {
          background: lightblue; }
  .panel .messageSender {
    position: relative;
    flex: 1 1; }
    .panel .messageSender textarea {
      display: flex;
      width: 100%;
      resize: none;
      height: 100%;
      outline: none;
      padding: 10px; }
    .panel .messageSender button {
      position: absolute;
      bottom: 10px;
      right: 10px;
      background: lightblue;
      padding: 5px 10px;
      border: none;
      border-radius: 10px; }

.chat {
  background: white;
  display: flex;
  box-shadow: 0 0 10px #ddd;
  border-radius: 4px;
  height: 600px; }

